<template>
    <views class="verify">
        <nav-bar
            :title="moduleTitle"
            @click-left="onClickLeft">
        </nav-bar>
        <views class="form">
            <views class="form-text mt280">
                <input
                    v-model="form.code"
                    :placeholder="placeholderTitle"
                    type="number"
                    pattern="[0-9]*"
                    class="text-center">
            </views>
            <views class="form-button mt124">
                <button @click="handleSubmit">校验</button>
            </views>
        </views>
    </views>
</template>

<script>
import NavBar from '@/components/navBar'
import tools from '@/mixins/tools'
import api from '@/utils/api'
export default {
    name: 'Verify',
    mixins: [tools],
    components: {
        NavBar
    },
    data() {
        return {
            form: {
                code: ''
            }
        }
    },
    computed: {
        query() {
            return this.$route.query
        },
        // 是否为明码验证 1: 明码验证 2:暗码校验
        isPublic() {
            return this.query.type === '1'
        },
        // 是否为产品防伪
        isVerify() {
            return this.query.verify && this.query.verify === '1'
        },
        // 是否为抽奖
        isPrize() {
            return this.query.prize && this.query.prize === '1'
        },
        // 是否为产品追溯
        isTrace() {
            return this.query.trace && this.query.trace === '1'
        },
        moduleTitle() {
            return this.isPublic ? '明码校验' : '暗码校验'
        },
        placeholderTitle() {
            return this.isPublic ? '请输入包装袋二维码下方数字编码' : '请输入包装袋内12位纯数字编码'
        }
    },
    mounted() {
        document.body.addEventListener('focusout', this.scrollToTop)
    },
    destroyed() {
        document.body.removeEventListener('focusout', this.scrollToTop)
    },
    methods: {
        onClickLeft() {
            if (this.isPrize) {
                this.push('/')
            } else {
                this.back()
            }
        },
        handleSubmit() {
            const { code } = this.form
            if (!code) return this.notify(`请输入${this.isPublic ? '明码' : '暗码'}`)
            if (code.length !== 12) return this.notify(`请输入正确的12位${this.isPublic ? '明码' : '暗码'}`)
            if (this.isVerify) return this.handleVerify(code)
            if (this.isPrize) return this.handlePrize(code)
            if (this.isTrace) return this.handleTrace(code)
        },
        // 校验 - 产品防伪
        handleVerify(code) {
            let apiStr = ''
            const params = {}
            if (this.isPublic) {
                params.public_code = code
                apiStr = 'publicVerify'
            } else {
                params.public_code = this.query.public_code
                params.secret_code = code
                apiStr = 'matchVerify'
            }
            api[apiStr](params).then((res) => {
                this.push({
                    path: '/verify-result',
                    query: {
                        type: this.query.type,
                        verify: this.query.verify,
                        ...params,
                        ...res.data
                    }
                })
            })
        },
        // 校验 - 抽奖
        handlePrize(code) {
            let apiStr = ''
            const params = {}
            if (this.isPublic) {
                params.public_code = code
                apiStr = 'publicVerifyForGeneral'
            } else {
                params.public_code = this.query.public_code
                params.secret_code = code
                apiStr = 'matchVerifyForGeneral'
            }
            api[apiStr](params).then((res) => {
                if (this.isPublic) {
                    if (res.data.success) {
                        this.push(`/verify?type=2&prize=1&public_code=${code}`)
                        this.form = {}
                    } else {
                        this.push(`/verify-result?type=1&prize=1&public_code=${code}&success=false`)
                    }
                } else {
                    if (res.data.success) {
                        this.push(`/prize?public_code=${this.query.public_code}&secret_code=${code}`)
                    } else {
                        this.push(`/verify-result?type=2&prize=1&public_code=${params.public_code}&secret_code=${params.secret_code}&success=false`)
                    }
                }
            })
        },
        // 校验 - 追踪溯源
        handleTrace(code) {
            let apiStr = ''
            const params = {}
            if (this.isPublic) {
                params.public_code = code
                apiStr = 'publicVerifyForGeneral'
            } else {
                params.public_code = this.query.public_code
                params.secret_code = code
                apiStr = 'matchTrace'
            }
            api[apiStr](params).then((res) => {
                if (this.isPublic) {
                    if (res.data.success) {
                        this.push(`/verify?type=2&trace=1&public_code=${code}`)
                        this.form = {}
                    } else {
                        this.push(`/verify-result?type=1&trace=1&public_code=${code}&success=false`)
                    }
                } else {
                    this.push({
                        path: '/trace',
                        query: {
                            ...res.data
                        }
                    })
                }
            })
        }
    }
}
</script>

<style lang="scss" scoped>
@import './index.scss';
</style>
